.specialtyContainer {
  width: 170px;
  height: 200px;
  margin: 0;
}

p {
  margin-top: 5px;
}

.title {
  font-weight: 500;
  font-size: 14px;
}

.sub {
  font-weight: lighter;
  font-size: 14px;
}

@media screen and (max-width: 420px) {
  .title {
    font-size: 13px;
  }

  .sub {
    font-size: 13px;
  }
}
@media screen and (max-width: 360px) {
  .title {
    font-size: 12px;
  }

  .sub {
    font-size: 10px;
  }
}