.landing {
  width: 100%;
  height: 100vh;
  margin-bottom: -200px;
  position: relative;
}

.Herofallback {
  background-image: url("/static/images/hero/landing.jp2");
  height: 100vh;
}

.IndexHero {
  background-image: url("/static/images/hero/landing.webp");
  height: 100vh;
}

.textContainer {
  background: rgba(0, 0, 0, 0.42);
  margin: auto auto;
  padding: 15px;
  width: 830px;
  height: 280px;
  color: white;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.appear {
  animation: fadein 0.65s ease-in forwards;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.Welcome {
  font-family: "DancingScript";
  font-style: normal;
  font-weight: bold;
  font-size: 96px;
  margin-bottom: 30px;
}

.Name {
  font-weight: 300;
  font-size: 36px;
}

.infoContainer {
  width: 100%;
  border: solid green 2px;
}

.Job {
  font-weight: 300;
  font-size: 48px;
}

@media screen and (max-width: 1024px) {
  .IndexHero {
    background-position: center;
  }
}
@media screen and (max-width: 770px) {
  .IndexHero {
    background-position: center;
  }

  .textContainer {
    padding: 20px;
    width: 580px;
    height: 180px;
  }

  .Welcome {
    font-size: 64px;
    margin-bottom: 10px;
  }

  .Name {
    font-size: 20px;
  }

  .Job {
    font-size: 24px;
  }
}
@media screen and (max-width: 420px) {
  .IndexHero {
    background-image: url("/static/images/hero/landing_mobile.png");
    background-position: center;
  }

  .textContainer {
    padding: 10px;
    width: 350px;
    height: 220px;
  }

  .Welcome {
    font-size: 52px;
    margin-bottom: 10px;
  }

  .Name {
    font-size: 18px;
  }

  .Job {
    font-size: 20px;
  }
}